import React, { Component } from 'react';
import { connect } from 'react-redux';
import { NavBar, Icon, ActivityIndicator } from 'antd-mobile';
import ReactDOM from 'react-dom';
import Content from '../../views/common/Content';
import { 
    goBackAction, 
    setScrollerPositionAction,
} from '../../store/actions/overview/listAction';

import '../../assets/css/overview.css';
// import overviewList from '../../assets/config/overview.js';

class OverviewListScreen extends Component
{
    constructor(props)
    {
        super(props);
        this.state = {
            isFetching: true,
        };
    }
    componentDidMount()
    {
        this.loadData();
    }
    render()
    {
        let overviewList = this.state.overviewList;
        return (
            <div>
                <NavBar mode="dark" 
                    className="navBar"
                    icon={<Icon type="left" />}
                    onLeftClick={() => {
                        let dispatch = this.props.dispatch;
                        this.props.history.goBack();
                        dispatch(goBackAction());
                    }}>
                    新县概览
                </NavBar>
                <Content ref={(view)=>{this.scrollView = view;}}>
                    {
                        overviewList ? overviewList.map((item, index)=>this.renderOverView(item, index, index === overviewList.length - 1)) : null
                    }
                    {
                        this.state.isFetching
                            ?
                            <ActivityIndicator 
                                toast
                                text="Loading..." />
                            :null
                    }
                </Content>
            </div>
        );
    }
    renderOverView(item, index, isLast)
    {
        return (
            <div key={index} 
                className={isLast ? 'overviewListItem overviewListLastItem' : 'overviewListItem'} 
                style={{backgroundImage: `url(${item.icon})`}}
                onClick={()=>this.showDetail(index)}>
            </div>
        );
    }
    loadData()
    {
        import('../../assets/config/overview.js').then(({ overviewList })=>{
            this.setState({
                overviewList,
                isFetching: false,
            }, ()=>{
                this.restoreScrollViewPosition();
            });
        });
    }
    showDetail(index)
    {
        this.cacheScrollOffset();
        this.props.history.push(`/overview/detail/${index}`);
    }
    cacheScrollOffset()
    {
        let offsetY = ReactDOM.findDOMNode(this.scrollView).scrollTop;
        this.props.dispatch(setScrollerPositionAction(offsetY));
    }
    restoreScrollViewPosition()
    {
        if(this.scrollView)
        {
            if(!isNaN(this.props.scrollViewOffset))
            {
                this.scrollView.scrollToY(this.props.scrollViewOffset);
            }
            else
            {
                this.scrollView.scrollToY(0);
            }
        }
    }
}

const mapStateToProps = function(state, ownProps)
{
    let obj = state.overview.listScreen;
    return {
        ...obj,
    };
};

OverviewListScreen = connect(mapStateToProps)(OverviewListScreen);

export default OverviewListScreen;


